.container__main{
width: 85%;
height:90%;
background-color:$containerColor;
position: absolute;
right: 0;
top: 50%;
transform: translateY(-50%);
display: grid;
grid-template-columns: 1fr;
grid-template-rows: 0.4fr 5fr;
grid-row-gap: 10px;
&__topBar{


display: grid;
grid-template-columns: 0.4fr 0.3fr;
justify-content: space-between;
&__search{
    background-color:$searchBarBackground;
    display: flex;
align-items: center;
  padding: 0 0.7rem;
  border-radius: 0.9rem;
    &__input{
        width: 100%;
        height: 100%;
        border: none;
        padding: 0 0.7rem;
        font-size: 1.2rem;
        background-color: $searchBarBackground;
    
    }
}
&__user{
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    &__p{
        font-size: 1.2rem;
        position: relative;
        font-family:Georgia, 'Times New Roman', Times, serif;
        left: -15px;
    }
    &__svg{
        position: relative;
        left: -30px;
    }
}

}






&__box{
  
    
    display: grid;
    grid-template-columns: 1fr 0.7fr;
    column-gap: 2rem;
grid-column-gap: 1fr;
    &__left{
   
        display: grid;
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1.3fr 1.5fr;
 
        &__welcome{
          background-image: url(/assets/4853433.jpg);
          background-position: center;
          background-size: cover;
           background-repeat: no-repeat;
           padding-left: 2rem;
         padding-top: 0.3rem;
         padding-bottom: 0.3rem;
           border-radius: 1.7rem;
           line-height: 1rem;
           display: flex;
           flex-direction: column;
           justify-content: space-evenly;
           &__h1{
              padding-top: 0.7rem;
               color: #f77f00;
               font-size: 1.7rem;
               font-family:$fontLato;
           }
           &__p{
               position: relative;
               color: #a37d31;
               font-family: $fontArvo;
             padding-bottom: 0.6rem;
             &:nth-of-type(2){
                 top: -10px;
             }

              
           }
    &__span{
        position: relative;
        left: -7px;
        display: flex;
        align-items: center;
     font-weight: bolder;
     font-size: 1.5rem;
        &__insideSpan{
         color: rgb(57, 69, 80);
            margin-left: 1rem;
            font-size: 0.8rem;
            font-family: $fontArvo;
            font-weight: bold;
        }
        &:nth-of-type(2){
            left: -4px;
        }
      
    }
        }
        &__georgeHome{
      margin-top:1rem;
         display: grid;
         grid-template-columns:1fr;
         grid-template-rows: 0.5fr 1fr;
         &__living{
            
             display: grid;
             grid-template-columns: 0.8fr 1fr;
             &__divOne{
              
                 display: flex;
                 align-items: center;
                 &__h1{
                     font-size: 1.5rem;
                     font-family: $fontArvo;
                 }
             }
             &__divTwo{
               
                 display: flex;
                 justify-content: space-between;
            
                 align-items: center;
                 &__span{
                     display: flex;
                     align-items: center;
                     font-family: $fontArvo;
                 }
                 &__roomSelect{
                     width: 140px;
                     height: 50%;
                     border-radius: 0.7rem;
                     background-color: $searchBarBackground;
                     display: flex;
                     align-items: center;
                     padding: 0 0.2rem;
                 }
             }
         }
         &__device{
             margin-top: 0.5rem;
             margin-bottom: 2rem;
             display: grid;
             grid-template-columns: 1fr 1fr 1fr 1fr;
           
             grid-column-gap: 1.5rem;
           &__div{
             
               border-radius: 1.3rem;
               display: grid;
               grid-template-columns: 1fr;
               grid-template-rows: 1.2fr 1.7fr 1.2fr;
          
             
               &__top{
             
               background-color: $navActive;
               border-top-left-radius: 1.3rem;
               border-top-right-radius: 1.3rem;
               display: flex;
               justify-content: space-around;
               align-items: center;
               &__span{
              
                   text-transform: uppercase;
                   font-family: $fontLato;
                   font-weight: 700;
               
               }
                 
               }
               &__middle{
                   background-color: $home;
                 &__svg{
                     position: relative;
                     left:6.4%;
                     top: 50%;
                     transform: translateY(-50%);
                     &.air{
                         fill:$airconIconColor;
                     }
                 }
               }
               &__bottom{
                   display: flex;
                   align-items: center;
                   background-color: $home;
                   border-bottom-left-radius: 1.3rem;
                   border-bottom-right-radius: 1.3rem;
              
                
                   &__p{
                     
                      position: relative;
                      left: 10%;
                      font-family: $fontArvo;
                      font-size: 0.8rem;
                      color: $navColor;
                   }
               }
            }
         }
        }
        &__temperature{
            background-color:$home;
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;
            &__status{
                width: 40%;
                height: 17%;
                position: absolute;
                top: 10px;
                left: 20px;
                display: flex;
                align-items: center;
             
                &__icon{
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 37px;
                    height: 37px;
                    border-radius: 50%;
                    background-color:$home2;
                    padding: 0.4rem;
                }
                &__text{
                    display: flex;
                    align-items: center;
                &__p{
                    margin-left: 0.7rem;
                    font-family: $fontArvo;
                    font-size: 0.8rem;
                    color: $navColor;
                }


                }
            }
            &__switch{
    
                width: 15%;
                height: 17%;
                position: absolute;
                top: 10px;
                right: 20px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                &__p{

                    text-transform: uppercase;
                    font-family: $fontLato;
                    font-weight: 700;

                }
            }
        }
    }
    &__right{
        background-color: hotpink;
    }
}
}